*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  user-select: none;
}

body {
  background-color: #fff;
  font-family: "Circe";
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

input {
  padding: 0;
  margin: 0;
  border: 0;
  border: none;
}

button {
  padding: 0;
  border: none;
  cursor: pointer;
}

.containers {
  max-width: 1440px;
  padding: 0 120px;
  margin: 0 auto;
  width: 100%;
}

ol,
ul {
  padding-left: 0;
}

dl,
ol,
ul {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
}

.display-n {
  display: none !important;
}

.opacity-d {
  opacity: 0 !important;
  visibility: hidden !important;
  transition: all 0.3s ease;
}

.opacity {
  opacity: 0 !important;
  transition: all 0.5s ease;
}

.header {
  width: 100%;
  padding: 30px 0;
  background: transparent;
  position: relative;
  z-index: 10;
}

.header__m {
  width: 32px;
  height: 32px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  display: none;
}

.header__description {
  position: absolute;
  top: 26px;
  left: 131px;
  transition: all 0.3s ease;
  opacity: 1;
  visibility: visible;
  width: 645px;
  padding: 47px 38px;
  background: #FFFFFF;
  box-shadow: 0px 4px 100px rgba(0, 0, 0, 0.25);
  border-radius: 0px 100px 100px 100px;
}

.header__description p {
  font-family: TTNorms;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 32px;
  color: #2C2C2C;
}

.header__block {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__block-w .header__logo img:nth-child(1) {
  display: none;
}

.header__block-w .header__logo img:nth-child(2) {
  display: block;
}

.header__block-w .header__i a {
  color: #ffffff;
}

.header__block-w .header__m img:nth-child(1) {
  display: none;
}

.header__block-w .header__m img:nth-child(2) {
  display: block;
}

.header__block-b .header__logo img:nth-child(1) {
  display: block;
}

.header__block-b .header__logo img:nth-child(2) {
  display: none;
}

.header__block-b .header__i a {
  color: #000000;
}

.header__block-b .header__m img:nth-child(1) {
  display: block;
}

.header__block-b .header__m img:nth-child(2) {
  display: none;
}

.header__logo {
  cursor: pointer;
  height: 50px;
  position: relative;
}

.header__l {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 750px;
}

.header__i a {
  font-family: TTNorms;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  color: #181818;
  transition: all 0.5s ease;
}

.footer {
  width: 100%;
  padding: 40px 0;
  background: #fff;
  position: fixed;
  z-index: 1;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0%);
}

.footer__block {
  height: 460px;
  width: 100%;
  background: transparent;
  position: relative;
  z-index: -5;
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
}

.footer__nav {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer__nav-logo {
  cursor: pointer;
}

.footer__nav-l {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 660px;
}

.footer__nav-i a {
  font-family: TTNorms;
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 19px;
  color: #111111;
}

.footer__info {
  margin: 60px 0 0 0;
  display: flex;
  width: 100%;
  align-items: flex-start;
  justify-content: space-between;
  padding: 0 140px 0 0;
}

.footer__info-c {
  width: 460px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.footer__info-c p {
  font-family: TTNorms;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: rgba(62, 62, 62, 0.6);
}

.footer__info-c a {
  margin: 33px 0 0 0;
  font-family: Roboto;
  font-style: normal;
  font-weight: 700;
  font-size: 34px;
  line-height: 46px;
  color: #313131;
}

.footer__info-c h5 {
  margin: 20px 0 0 0;
  font-family: TTNorms;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 26px;
  color: #111111;
}

.footer__info-a {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 280px;
}

.footer__info-a p {
  font-family: TTNorms;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: rgba(62, 62, 62, 0.6);
}

.footer__info-a address {
  font-family: TTNorms;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 26px;
  color: #111111;
  margin: 32px 0 0 0;
}

.footer__add {
  margin: 70px 0 0 0;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}

.footer__add-i {
  font-family: TTNorms;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 22px;
  color: #9dafbd;
}

.footer__add-c {
  cursor: pointer;
  font-family: TTNorms;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 22px;
  color: #9dafbd;
}

.form__e {
  border: 2px solid red !important;
}

.form__b {
  width: 645px;
  padding: 73px 61px 55px 61px;
  background: #00a3ff;
  border-radius: 100px 0px 0px 0px;
}

.form__b-n {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.form__b-name {
  width: max-content;
  height: max-content;
  background: #00a3ff;
  border: 2px solid rgba(255, 255, 255, 0.71);
  border-radius: 100px;
  padding: 15px 20px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.form__b-name input {
  width: 200px;
  border: none;
  background: transparent;
  font-family: TTNorms;
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 200%;
  color: #ffffff;
  border: none;
  outline: none;
}

.form__b-name input:focus {
  border: none;
  outline: none;
}

.form__b-name input::placeholder {
  font-family: TTNorms;
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 200%;
  color: #ffffff;
}

.form__b-phone {
  width: max-content;
  height: max-content;
  background: #00a3ff;
  border: 2px solid rgba(255, 255, 255, 0.71);
  border-radius: 100px;
  padding: 15px 20px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.form__b-phone input {
  width: 200px;
  border: none;
